"use client";

import React, { useRef, useState, useEffect } from 'react';
import { VideoFile } from '@/types';
import { Play, Pause, Volume2, VolumeX, Maximize, Minimize, ArrowLeft, Settings, MessageSquarePlus } from 'lucide-react';
import styles from './NetPlayer.module.css';
import { srtToWebvtt } from '@/utils/subtitle';

interface NetPlayerProps {
    video: VideoFile;
    onBack: () => void;
}

export default function NetPlayer({ video, onBack }: NetPlayerProps) {
    const videoRef = useRef<HTMLVideoElement>(null);
    const containerRef = useRef<HTMLDivElement>(null);
    const subtitleInputRef = useRef<HTMLInputElement>(null);

    const [isPlaying, setIsPlaying] = useState(false);
    const [progress, setProgress] = useState(0);
    const [volume, setVolume] = useState(1);
    const [isMuted, setIsMuted] = useState(false);
    const [isFullscreen, setIsFullscreen] = useState(false);
    const [showControls, setShowControls] = useState(true);
    const [subtitleUrl, setSubtitleUrl] = useState<string | null>(null);

    // Hide controls timer
    const controlsTimeoutRef = useRef<NodeJS.Timeout | null>(null);

    useEffect(() => {
        // Auto play on mount
        if (videoRef.current) {
            videoRef.current.play().catch(e => console.log('Autoplay blocked', e));
            setIsPlaying(true);
        }
    }, []);

    const handleMouseMove = () => {
        setShowControls(true);
        if (controlsTimeoutRef.current) clearTimeout(controlsTimeoutRef.current);
        controlsTimeoutRef.current = setTimeout(() => {
            if (isPlaying) setShowControls(false);
        }, 3000);
    };

    const togglePlay = () => {
        if (videoRef.current) {
            if (videoRef.current.paused) {
                videoRef.current.play();
                setIsPlaying(true);
            } else {
                videoRef.current.pause();
                setIsPlaying(false);
            }
        }
    };

    const handleTimeUpdate = () => {
        if (videoRef.current) {
            const current = videoRef.current.currentTime;
            const duration = videoRef.current.duration || 1;
            setProgress((current / duration) * 100);
        }
    };

    const handleSeek = (e: React.ChangeEvent<HTMLInputElement>) => {
        const val = parseFloat(e.target.value);
        if (videoRef.current) {
            const duration = videoRef.current.duration || 1;
            videoRef.current.currentTime = (val / 100) * duration;
            setProgress(val);
        }
    };

    const toggleVolume = () => {
        if (videoRef.current) {
            videoRef.current.muted = !isMuted;
            setIsMuted(!isMuted);
        }
    };

    const handleVolumeChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        const val = parseFloat(e.target.value);
        setVolume(val);
        if (videoRef.current) {
            videoRef.current.volume = val;
            setIsMuted(val === 0);
        }
    };

    const toggleFullscreen = () => {
        if (!document.fullscreenElement) {
            containerRef.current?.requestFullscreen();
            setIsFullscreen(true);
        } else {
            document.exitFullscreen();
            setIsFullscreen(false);
        }
    };

    const formatTime = (seconds: number) => {
        if (!seconds) return "0:00";
        const date = new Date(seconds * 1000);
        const hh = date.getUTCHours();
        const mm = date.getUTCMinutes();
        const ss = date.getUTCSeconds().toString().padStart(2, '0');
        if (hh) {
            return `${hh}:${mm.toString().padStart(2, '0')}:${ss}`;
        }
        return `${mm}:${ss}`;
    };

    const handleSubtitleClick = () => {
        subtitleInputRef.current?.click();
    };

    const handleSubtitleChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            const file = e.target.files[0];
            const text = await file.text();
            // Detect if SRT
            let vttContent = text;
            if (file.name.endsWith('.srt')) {
                vttContent = srtToWebvtt(text);
            }

            if (subtitleUrl) URL.revokeObjectURL(subtitleUrl);

            const blob = new Blob([vttContent], { type: 'text/vtt' });
            const url = URL.createObjectURL(blob);
            setSubtitleUrl(url);

            // Force track update
            if (videoRef.current) {
                // Clear existing tracks if any custom ones?
                // For now just one custom track supported
            }
        }
    };

    useEffect(() => {
        return () => {
            if (subtitleUrl) URL.revokeObjectURL(subtitleUrl);
        };
    }, [subtitleUrl]);

    return (
        <div
            ref={containerRef}
            className={styles.playerContainer}
            onMouseMove={handleMouseMove}
            onMouseLeave={() => isPlaying && setShowControls(false)}
        >
            <video
                ref={videoRef}
                src={video.url}
                className={styles.video}
                onClick={togglePlay}
                onTimeUpdate={handleTimeUpdate}
                onEnded={() => setIsPlaying(false)}
            >
                {subtitleUrl && <track kind="subtitles" src={subtitleUrl} label="Custom" default />}
            </video>

            <input
                type="file"
                ref={subtitleInputRef}
                onChange={handleSubtitleChange}
                accept=".srt,.vtt"
                style={{ display: 'none' }}
            />

            {/* Overlay Gradient at bottom */}
            <div className={`${styles.controlsOverlay} ${!showControls ? styles.hidden : ''}`}>

                {/* Top Bar (Back button) */}
                <div className={styles.topBar}>
                    <button onClick={onBack} className={styles.iconBtn}>
                        <ArrowLeft size={32} />
                    </button>
                    <h3 className={styles.title}>{video.name}</h3>
                </div>

                {/* Center Play Button (Optional - Netflix usually doesn't have a giant one in center unless paused, but good for UX) */}
                {!isPlaying && (
                    <div className={styles.centerPlay} onClick={togglePlay}>
                        <Play size={64} fill="white" />
                    </div>
                )}

                {/* Bottom Controls */}
                <div className={styles.bottomBar}>
                    {/* Progress Bar */}
                    <div className={styles.progressContainer}>
                        <input
                            type="range"
                            className={styles.progressBar}
                            min="0"
                            max="100"
                            step="0.1"
                            value={progress}
                            onChange={handleSeek}
                            style={{ backgroundSize: `${progress}% 100%` }}
                        />
                    </div>

                    <div className={styles.controlsRow}>
                        <div className={styles.leftControls}>
                            <button onClick={togglePlay} className={styles.iconBtn}>
                                {isPlaying ? <Pause fill="white" /> : <Play fill="white" />}
                            </button>

                            <button onClick={() => {
                                if (videoRef.current) videoRef.current.currentTime -= 10;
                            }} className={styles.textBtn}>-10s</button>

                            <button onClick={() => {
                                if (videoRef.current) videoRef.current.currentTime += 10;
                            }} className={styles.textBtn}>+10s</button>

                            <div className={styles.volumeContainer}>
                                <button onClick={toggleVolume} className={styles.iconBtn}>
                                    {isMuted || volume === 0 ? <VolumeX /> : <Volume2 />}
                                </button>
                                <input
                                    type="range"
                                    min="0"
                                    max="1"
                                    step="0.05"
                                    value={isMuted ? 0 : volume}
                                    onChange={handleVolumeChange}
                                    className={styles.volumeSlider}
                                />
                            </div>

                            <span className={styles.timeDisplay}>
                                {videoRef.current ? formatTime(videoRef.current.currentTime) : "0:00"}
                                {" / "}
                                {videoRef.current ? formatTime(videoRef.current.duration) : "0:00"}
                            </span>
                        </div>

                        <div className={styles.rightControls}>
                            <button className={styles.iconBtn} onClick={handleSubtitleClick} title="Add Subtitles">
                                <MessageSquarePlus />
                            </button>
                            <button onClick={toggleFullscreen} className={styles.iconBtn}>
                                {isFullscreen ? <Minimize /> : <Maximize />}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
